import { CHANGE_PAGE } from './actions.js';

const initialState = {
    page: 'homepage'
}

function eatlyApp(state = initialState, action) {
    switch(action.type) {
        case CHANGE_PAGE:
            return Object.assign({}
                                 , state
                                 , {
                                     page: action.page
                                 })
        default:
            return state
    }
}

export default eatlyApp